.video {
  position: relative;
  width: 100%;

  &__video {
    position: relative;
    display: block;
    margin: 0 auto;
    height: 166px;
    // max-width: $deviceChangerPhone;

    @media (min-width: $deviceChangerTablet) {
      display: block;
      height: 400px;
      margin: 0 auto;
    }

    @media (min-width: $deviceChangerDesktop) {
      display: block;
      height: 590px;
      margin: 0 auto;
    }
  }

